import { useState } from "react"
import { ethers, BigNumber } from "ethers"
import { Box, Button, Flex, Input, Text } from "@chakra-ui/react"
// import { BigNumber } from "@ethersproject/bignumber"
import roboPunksNFT from "./RoboPunksNFT.json"
import Swal from "sweetalert2"
import "@sweetalert2/theme-dark"

const roboPunksNFTAddress = "0x4F877Ec3a356D4896B8470DDC72e600Af4A94066"

const MainMint = ({ accounts, setAccounts }) => {
    const [mintAmount, setMintAmount] = useState(1)
    const targetNetworkId = "0xaa36a7"
    const isConnected = Boolean(accounts[0])
    const [isLoading, setIsLoading] = useState(false)
    // const [isTransactionConfirmed, setIsTransactionConfirmed] = useState(false)

    async function handleMint() {
        if (window.ethereum) {
            const currentChainId = await window.ethereum.request({
                method: "eth_chainId",
            })

            // return true if network id is the same
            if (currentChainId === targetNetworkId) {
                const provider = new ethers.providers.Web3Provider(
                    window.ethereum
                )
                const signer = provider.getSigner()
                // const blockNum = await provider.getBlockNumber()
                // console.log(blockNum)
                const contract = new ethers.Contract(
                    roboPunksNFTAddress,
                    roboPunksNFT.abi,
                    signer
                )
                try {
                    setIsLoading(true)
                    const response = await contract.mint(
                        BigNumber.from(mintAmount),
                        {
                            value: ethers.utils.parseEther(
                                (0.02 * mintAmount).toString()
                            ),
                        }
                    )
                    console.log("response: ", response)

                    function transactionConfirmedTimer() {
                        const Toast = Swal.mixin({
                            toast: true,
                            position: "bottom-end",
                            showConfirmButton: false,
                            timer: 3000,
                            timerProgressBar: true,
                            didOpen: (toast) => {
                                toast.addEventListener(
                                    "mouseenter",
                                    Swal.stopTimer
                                )
                                toast.addEventListener(
                                    "mouseleave",
                                    Swal.resumeTimer
                                )
                            },
                        })

                        Toast.fire({
                            icon: "success",
                            title: "NFT Mint Success",
                        })
                    }

                    window.setTimeout(transactionConfirmedTimer, 7000)

                    function isLoadingTimerrr() {
                        return setIsLoading(false)
                    }
                    window.setTimeout(isLoadingTimerrr(), 5000)
                } catch (err) {
                    if (err.code === "INSUFFICIENT_FUNDS") {
                        // window.alert("insufficient balance")
                        Swal.fire({
                            icon: "error",
                            title: "Insufficient Funds",
                            text: "You can get some Sepolia Faucet from Here",
                            confirmButtonText: "Get Faucet",
                        }).then(() => {
                            window.open("https://sepoliafaucet.com/", "_blank")
                        })
                        function isLoadingTimerrr() {
                            return setIsLoading(false)
                        }
                        window.setTimeout(isLoadingTimerrr(), 5000)
                    } else if (err.code === "ACTION_REJECTED") {
                        // window.alert("Transaction Rejected")
                        const Toast = Swal.mixin({
                            toast: true,
                            position: "bottom-end",
                            showConfirmButton: false,
                            timer: 3000,
                            timerProgressBar: true,
                            didOpen: (toast) => {
                                toast.addEventListener(
                                    "mouseenter",
                                    Swal.stopTimer
                                )
                                toast.addEventListener(
                                    "mouseleave",
                                    Swal.resumeTimer
                                )
                            },
                        })

                        Toast.fire({
                            icon: "warning",
                            title: "Transaction Cancled",
                        })
                        function isLoadingTimerrr() {
                            return setIsLoading(false)
                        }
                        window.setTimeout(isLoadingTimerrr(), 5000)
                    }
                    // console.log(err.code)
                    // console.log("error: ", err)
                }
                // finally {
                //     function isLoadingTimer() {
                //         return setIsLoading(false)
                //     }
                //     window.setTimeout(isLoadingTimer(), 5000)
                // }
            } else {
                // 11155111
                const chainId = 11155111
                // return false is network id is different
                Swal.fire({
                    icon: "warning",
                    title: "Wrong Network",
                    text: "Please switch to Sepolia Testnet",
                    confirmButtonText: "Switch To Sepolia",
                }).then(async () => {
                    if (window.ethereum.networkVersion !== chainId) {
                        try {
                            await window.ethereum.request({
                                method: "wallet_switchEthereumChain",
                                params: [
                                    {
                                        chainId: targetNetworkId,
                                    },
                                ],
                            })
                        } catch (err) {
                            // This error code indicates that the chain has not been added to MetaMask
                            if (err.code === 4902) {
                                await window.ethereum.request({
                                    method: "wallet_addEthereumChain",
                                    params: [
                                        {
                                            chainName: "Sepolia Testnet",
                                            chainId: targetNetworkId,
                                            nativeCurrency: {
                                                name: "Sepolia test network",
                                                decimals: 11155111,
                                                symbol: "SepoliaETH",
                                            },
                                            rpcUrls: [
                                                "https://sepolia.infura.io/v3/",
                                            ],
                                        },
                                    ],
                                })
                            }
                        }
                    }
                })
            }
        }
    }

    const handleDecrement = () => {
        if (mintAmount <= 1) return
        setMintAmount(mintAmount - 1)
    }

    const handleIncrement = () => {
        if (mintAmount >= 3) return
        setMintAmount(mintAmount + 1)
    }

    return (
        <Flex
            justify="center"
            align="center"
            height="100vh"
            paddingBottom="150px"
        >
            <Box width="520px">
                <div>
                    <Text fontSize="48px" textShadow="0 5px #000000">
                        RoboPunks
                    </Text>
                    <Text
                        fontSize="30px"
                        letterSpacing="-5.5%"
                        fontFamily="VT323"
                        textShadow="0 2px 2px #000000"
                    >
                        It's 2078. Can the RoboPunks NFT save humans from
                        destructive rampant NFT speculation? Mint Robopunks to
                        find out.
                    </Text>
                </div>
                {isConnected ? (
                    <div>
                        <Flex align="center" justify="center">
                            <Button
                                backgroundColor="#D6517D"
                                borderRadius="5px"
                                boxShadow="0px 2px 2px 1px #0F0F0F"
                                color="white"
                                cursor="pointer"
                                fontFamily="inherit"
                                padding="15px"
                                marginTop="10px"
                                onClick={handleDecrement}
                            >
                                -
                            </Button>
                            <Input
                                readOnly
                                fontFamily="inherit"
                                width="100px"
                                height="40px"
                                textAlign="center"
                                paddingLeft="19px"
                                marginTop="10px"
                                type="number"
                                value={mintAmount}
                            />
                            <Button
                                backgroundColor="#D6517D"
                                borderRadius="5px"
                                boxShadow="0px 2px 2px 1px #0F0F0F"
                                color="white"
                                cursor="pointer"
                                fontFamily="inherit"
                                padding="15px"
                                marginTop="10px"
                                onClick={handleIncrement}
                            >
                                +
                            </Button>
                        </Flex>
                        <Button
                            backgroundColor="#D6517D"
                            borderRadius="5px"
                            boxShadow="0px 2px 2px 1px #0F0F0F"
                            color="white"
                            cursor="pointer"
                            fontFamily="inherit"
                            padding="15px"
                            marginTop="10px"
                            onClick={handleMint}
                            disabled={isLoading}
                        >
                            {isLoading ? "Minting..." : "Mint"}
                        </Button>
                    </div>
                ) : (
                    <Text
                        marginTop="70px"
                        fontSize="30px"
                        letterSpacing="-5.5%"
                        fontFamily="VT323"
                        textShadow="0 3px #000000"
                        color="#D6517D"
                    >
                        You must be connected to Mint.
                    </Text>
                )}
            </Box>
        </Flex>
    )
}

export default MainMint
